<template>
  <Row>
    <Col span="21">
      <div v-highlight>
        <div class="i-editor-wrapper"><div class="i-editor-md"> <div class="dev-md-content"><h3 id="YR_vNode">引入vue-task-node <a href="#YR_vNode">#</a></h3><p>一般在 webpack 入口页面 main.js 中如下配置：</p>
          <pre><code class="lang-js">import Vue from 'vue';
            import VueRouter from 'vue-router';
            import Routers from './router.js';
            import VueTaskNode from 'vue-task-node'
            import "vue-task-node/dist/css/vnode.css"

            Vue.use(VueRouter);
            Vue.use(VueTaskNode);

            // The routing configuration
            const RouterConfig = {
                routes: Routers
            };
            const router = new VueRouter(RouterConfig);

            new Vue({
                el: '#app',
                router: router,
                render: h =&gt; h(App)
            });
            </code></pre>
        </div></div></div>
      </div>
    </Col>
    <Col span="3">
    <Anchor show-ink>
      <AnchorLink v-for="item in lins" :key="item.uri" :href="'#'+ item.uri" :title="item.title" />
    </Anchor>
    </Col>
  </Row>
</template>
<script>
  export default {
    data () {
      return {
        lins:[{
          uri:'YR_vNode',
          title:'引入vue-task-node'
        }]
      }
    }
  }
</script>



