import Vue from 'vue'
import Router from 'vue-router'
import Install from '@/views/install'
import Area from '@/views/workarea'
import InitialNode from '@/views/initialnode'
import Start from '@/views/start'
import Node from '@/views/node'
import NodeExp from '@/views/nodeexp'
import CommonNode from '@/views/commonnode'
import LPath from '@/views/lpath'
import CurvePath from '@/views/curvepath'
import InPort from '@/views/inport'
import OutPort from '@/views/outport'
import Update from '@/views/update'
import About from '@/views/about'
import Demo from '@/views/demo'
import Cmp from '@/views/cmp'
import App from '@/views/app'
Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'app',
      component: App
    },
    {
      path: '/install',
      name: 'install',
      component: Install
    },
    {
      path: '/warkarea',
      name: 'WarkArea',
      component: Area
    },
    {
      path: '/initialnode',
      name: 'InitialNode',
      component: InitialNode
    },
    {
      path: '/start',
      name: 'Start',
      component: Start
    },
    {
      path: '/node',
      name: 'Node',
      component: Node
    },
    {
      path: '/nodeexp',
      name: 'NodeExp',
      component: NodeExp
    },
    {
      path: '/commonnode',
      name: 'CommonNode',
      component: CommonNode
    },
    {
      path: '/lpath',
      name: 'LPath',
      component: LPath
    },
    {
      path: '/curvepath',
      name: 'CurvePath',
      component: CurvePath
    },
    {
      path: '/inport',
      name: 'InPort',
      component: InPort
    },
    {
      path: '/outport',
      name: 'OutPort',
      component: OutPort
    },
    {
      path: '/update',
      name: 'Update',
      component: Update
    },
    {
      path: '/about',
      name: 'About',
      component: About
    },
    {
      path: '/demo',
      name: 'Demo',
      component: Demo
    },
    {
      path: '/cmp',
      name: 'Cmp',
      component: Cmp
    }
  ]
})



