<template>
  <Layout>
    <div>
    <Row class="wrapper-header">
      <Menu mode="horizontal"  v-on:on-select="onSelectTop" :theme="theme" :active-name="active($route.path)">
        <div class="layout-logo">
          <router-link to="/"><img src="../static/logo.png" style="width: 40px;height: 40px"></router-link>
        </div>
        <div class="layout-nav">
          <MenuItem name="sy">
            <Icon type="ios-navigate"></Icon>
            首页
          </MenuItem>
          <MenuItem name="zj">
            <Icon type="ios-keypad"></Icon>
            组件
          </MenuItem>
        </div>
      </Menu>
    </Row>
    </div>
    <Row style="background-color: #fff;">
      <Col span="3" ref="colLeft">
      <Menu v-on:on-select="onSelect" :theme="theme" active-name="1">
        <MenuItem v-for="item in MenuJson" :key="item.key" :name="item.key">
          <Icon :type="item.icon" />
          {{ item.name }}
        </MenuItem>
        <li class="ivu-menu-item" :style="'height:'+ sHeight + 'px'">
        </li>
      </Menu>
      </Col>
      <Col span="21" ref="colRight">
      <router-view></router-view>
      </Col>
    </Row>
  </Layout>
</template>
<script>
  import {menuJson} from './data/dataJson'
  export default {
    data () {
      return {
        theme: 'light',
        MenuJson: menuJson,
        sHeight:'300'
      }
    },
    methods:{
      onSelect:function(name){
        this.$router.push({ path: `/${name}`})
        var lel = this.$refs.colLeft
        var rel = this.$refs.colRight
        var l = lel.$el.offsetHeight
        var r = rel.$el.offsetHeight

        if(r > l){
          this.sHeight = r - 700
        }
      },
      onSelectTop:function(name){
        if(name === 'sy'){
          this.$router.push({ path: `/`})
        }else if( name === 'zj'){
          this.$router.push({ path: `/install`})
        }
      },
      active:function (value) {
          if(value === '/'){
            return 'sy'
          }else{
            return 'zj'
          }
      }
    },
    updated: function () {
      var lel = this.$refs.colLeft
      var rel = this.$refs.colRight
      var l = lel.$el.offsetHeight
      var r = rel.$el.offsetHeight

      if(r > l){
        this.sHeight = r - 700
      }
    }
  }
</script>
<style scoped>
  .layout-logo{
    width: 100px;
    height: 30px;
    background: #fff;
    border-radius: 3px;
    float: left;
    position: relative;
    top: 10px;
    left: 20px;
  }
  .layout-nav{
    width: 420px;
    margin: 0 auto;
    margin-right: 20px;
  }
</style>



