<template>
  <Row>
    <Col span="21">
      <div v-highlight>
        <div class="i-editor-wrapper">
          <div class="i-editor-md">
            <div class="dev-md-content"><h3 id="NPM_vNode">NPM安装 <a href="#NPM_vNode">#</a></h3>
              <pre><code class="lang-js">npm install vue-task-node -S
              </code></pre>
            </div>
          </div>
        </div>
      </div>
    </Col>
    <Col span="3">
    <Anchor show-ink>
      <AnchorLink v-for="item in lins" :key="item.uri" :href="'#'+ item.uri" :title="item.title" />
    </Anchor>
    </Col>
  </Row>
</template>
<script>
  export default {
    data() {
      return {
        lins:[{
          uri:'NPM_vNode',
          title:'NPM安装'
        }]
      }
    }
  }
</script>



