<template>
  <Row>
    <Col span="21">
      <Row v-highlight>
    <Col>
    <div class="i-editor-wrapper">
      <div class="i-editor-md">
        <div class="dev-md-content">
          <h3 id="GS_vNode">概述 <a href="#GS_vNode">#</a></h3>
          <p>
            <code>&lt;task-work-area&gt;</code>
            是一个工作区，也就是我们接下来的可拖拽的节点要，包含在此容器中，其 本质是一个通过组装的
            <code>&lt;svg&gt;</code>
            标签。
          </p>
          <h3 id="YF_vNode">用法 <a href="#YF_vNode">#</a></h3>
          <pre>
            <code class="lang-html">&lt;task-work-area width="500" height="500" id="work_id"&gt;
    &lt;!-- 这里就是包含节点、路径 等--&gt;
    &lt;/task-work-area&gt;
    </code>
          </pre>
          <h3 id="API_vNode">API <a href="#API_vNode">#</a></h3>
        </div>
      </div>
      <p>props参数</p><br>
      <Table border size="small" :columns="propsColumn" :data="propsData"></Table>
      <br><p>event事件</p><br>
      <Table border size="small" :columns="eventColumn" :data="eventData"></Table>
    </div>
    </Col>
  </Row>
    </Col>
    <Col span="3">
    <Anchor show-ink>
      <AnchorLink v-for="item in lins" :key="item.uri" :href="'#'+ item.uri" :title="item.title" />
    </Anchor>
    </Col>
  </Row>
</template>
<script>
  import ApiTable from '../mixinx/ApiTable'
  export default {
    mixins: [ ApiTable],
    data () {
      return {
        lins:[{
          uri:'GS_vNode',
          title:'概述'
        },{
          uri:'YF_vNode',
          title:'用法'
        },{
          uri:'API_vNode',
          title:'API'
        }],
        propsData:[{
          attribute: 'width',
          state: '工作区宽度',
          mold: 'String | Number',
          defau: '0'
        },{
          attribute: 'height',
          state: '工作区高度',
          mold: 'String | Number',
          defau: '0'
        },{
          attribute: 'id',
          state: '工作区ID',
          mold: 'String | Number',
          defau: '-'
        }],
        eventData: [{
          eventname: 'on-drag-over',
          exp: '节点拖拽过程中，拖拽持续移动事件',
          retu: '(event,所拖拽节点的数据)'
        },{
          eventname: 'on-mouse',
          exp: '鼠标右击工作区事件 ',
          retu: '(event,工作区ID)'
        }
        ]
      }
    }
  }
</script>




