<template>
  <Row>
    <Col span="21">
  <Row v-highlight>
    <Col>
    <div class="i-editor-wrapper"><div class="i-editor-md"> <div class="dev-md-content"><h3 id="YS_vNode">演示 <a href="#YS_vNode">#</a></h3></div></div></div>
      <div class="vnode-col" style="text-align: center">
        <task-work-area width=1000 height=500 id="areaID" v-on:on-drag-over="dragOver" v-on:on-mouse="mouseMenu" ref="area">
          <task-curve-path areaid="areaID" :paths="paths" ref="curve"></task-curve-path>
          <template v-for="node in nodes">
            <task-common-node :key="node.id" :node="node" v-on:on-select="selectlMethod" v-on:on-drag-start="dragStart" v-on:on-drag-end="dragEnd" :updateTem="updateCompleted" v-on:on-mouse="mouseNodeMenu"></task-common-node>
          </template>
          <task-initial-node :node="initialData" backgroundColor="#ff5722" v-on:on-select="selectlMethod" v-on:on-drag-start="dragStart" v-on:on-drag-end="dragEnd" :updateTem="updateCompleted" v-on:on-mouse="mouseNodeMenu"></task-initial-node>
        </task-work-area>
      </div>
    </Col>
    <Col>
    <div class="i-editor-wrapper"><div class="i-editor-md"> <div class="dev-md-content"><h3 id="GS_vNode">概述 <a href="#GS_vNode">#</a></h3><p><code>&lt;task-curve-path&gt;</code>是一个点到点的路径组，该组件中封装了<code>&lt;task-path&gt;</code>，其是根据<code>&lt;task-in-port&gt; 和 &lt;task-out-port&gt;</code> 的<code>VDOM</code>获取坐标点，来链接点的。</p>
      <h3 id="YF_vNode">用法 <a href="#YF_vNode">#</a></h3><pre><code class="lang-html">&lt;template&gt;
    &lt;div class="hello" style="text-align: center"&gt;
      &lt;task-work-area width=1000 height=500 id="areaID" v-on:on-drag-over="dragOver" v-on:on-mouse="mouseMenu" ref="area"&gt;
        &lt;task-curve-path areaid="areaID" :paths="paths" ref="curve"&gt;&lt;/task-curve-path&gt;
        &lt;template v-for="node in nodes"&gt;
          &lt;task-common-node :key="node.id" :node="node" v-on:on-select="selectlMethod" v-on:on-drag-start="dragStart" v-on:on-drag-end="dragEnd" :updateTem="updateCompleted" v-on:on-mouse="mouseNodeMenu"&gt;&lt;/task-common-node&gt;
        &lt;/template&gt;
        &lt;task-initial-node :node="initialData" backgroundColor="#ff5722" v-on:on-select="selectlMethod" v-on:on-drag-start="dragStart" v-on:on-drag-end="dragEnd" :updateTem="updateCompleted" v-on:on-mouse="mouseNodeMenu"&gt;&lt;/task-initial-node&gt;
      &lt;/task-work-area&gt;
    &lt;/div&gt;
&lt;/template&gt;

&lt;script&gt;
export default {
  name: 'App',
  data () {
    return {
      startNode: {},
      initialData: {
        id: 'node4',
        name: '节点4',
        positionX: 300,
        positionY: 60,
        icon: 'task-icon-41',
        inPorts: [],
        outPorts: [{
          id: 'node4_5'
        }]
      },
      nodes: [{
        id: 'node1',
        name: '节点1',
        positionX: 115,
        positionY: 180,
        state: 'success',
        inPorts: [{
          id: 'node1_1',
          isConnected: true
        }],
        outPorts: [{
          id: 'node1_4'
        }]
      }, {
        id: 'node2',
        name: '节点2',
        positionX: 20,
        positionY: 300,
        state: 'mistake',
        inPorts: [{
          id: 'node2_1',
          isConnected: true
        }, {
          id: 'node2_3',
          isConnected: false
        }],
        outPorts: [{
          id: 'node2_4'
        }, {
          id: 'node2_5'
        }]
      }, {
        id: 'node3',
        name: '节点3',
        positionX: 340,
        positionY: 315,
        state: 'running',
        inPorts: [{
          id: 'node3_1',
          isConnected: true
        }, {
          id: 'node3_3',
          isConnected: false
        }],
        outPorts: [{
          id: 'node3_4'
        }]
      }, {
        id: 'node5',
        name: '节点5',
        positionX: 420,
        positionY: 420,
        icon: 'task-icon-6',
        inPorts: [{
          id: 'node5_1',
          isConnected: true
        }],
        outPorts: []
      }],
      paths: [{
        dotted: true,
        ptype: 'Q',
        startPort: 'node4_5',
        endPort: 'node1_1'
      }, {
        dotted: true,
        ptype: 'L',
        startPort: 'node1_4',
        endPort: 'node2_1'
      }, {
        dotted: false,
        ptype: 'Q',
        startPort: 'node1_4',
        endPort: 'node3_1'
      }, {
        dotted: false,
        ptype: 'L',
        startPort: 'node3_4',
        endPort: 'node5_1'
      }]
    }
  },
  methods: {
    selectlMethod: function (event, data, node) {
      console.log(event, data, node)
    },
    dragStart: function (event, node) {
      let nodeData = event.dataTransfer.getData('nodedata')
      console.log('开始移动', event.clientX, event.clientY, node, JSON.parse(nodeData))
      this.startNode = {id: node.id, positionX: event.clientX, positionY: event.clientY}
    },
    dragEnd: function (event, node) {
      let me = this
      console.log('移动结束', event.clientX, event.clientY, node)
      this.nodes.forEach(function (item) {
        if (item.id === node.id) {
          item.positionX = node.positionX + (event.clientX - me.startNode.positionX)
          item.positionY = node.positionY + (event.clientY - me.startNode.positionY)
        }
      })
      if (node.id === this.initialData.id) {
        this.initialData.positionX = node.positionX + (event.clientX - me.startNode.positionX)
        this.initialData.positionY = node.positionY + (event.clientY - me.startNode.positionY)
      }
    },
    dragOver: function (event) {
      console.log('移动中...', event.clientX, event.clientY)
    },
    updateCompleted: function () {
      console.log('updateCompleted!!')
      // 重新加载路径
      this.$refs.curve.vReload() //必需添加的方法，实现节点拖拽后，路径重新渲染
    },
    mouseMenu: function (event, id) {
      console.log(event, id)
    },
    mouseNodeMenu: function (event, node) {
      console.log(event, node)
    }
  }
}
&lt;/script&gt;
</code></pre>
      <p>▲ <code>updateCompleted:function(){this.$refs.curve.vReload()}</code>必需添加，实现节点拖拽后，路径重新渲染。</p>
      <h3 id="API_vNode">API <a href="#API_vNode">#</a></h3></div></div>
      <p>props参数</p><br>
      <Table border size="small" :columns="propsColumn" :data="propsData"></Table>
      <br><p>method方法</p><br>
      <Table border size="small" :columns="methodColumn" :data="methodData"></Table>
    </div>
    <div class="i-editor-wrapper"><div class="i-editor-md"> <div class="dev-md-content">
      <h3 id="DATA_vNode">paths数据结构 <a href="#DATA_vNode">#</a></h3>
      <pre><code class="lang-js">paths: [{
        dotted: true,//虚线 或 实线
        ptype: 'Q', //曲线 或 折线
        startPort: 'node4_5', //开始点id
        endPort: 'node1_1'//结束点id
      },...]
</code></pre>
    </div></div></div>
    </Col>
  </Row>
  </Col>
    <Col span="3">
    <Anchor show-ink>
      <AnchorLink v-for="item in lins" :key="item.uri" :href="'#'+ item.uri" :title="item.title" />
    </Anchor>
    </Col>
  </Row>
</template>
<script>
  import ApiTable from '../mixinx/ApiTable'
  export default {
    mixins: [ ApiTable],
    data () {
      return {
        lins:[{
          uri:'YS_vNode',
          title:'演示'
        },{
          uri:'GS_vNode',
          title:'概述'
        },{
          uri:'YF_vNode',
          title:'用法'
        },{
          uri:'API_vNode',
          title:'API'
        },{
          uri:'DATA_vNode',
          title:'paths数据结构'
        }],
        startNode: {},
        initialData: {
          id: 'node4',
          name: '节点4',
          positionX: 300,
          positionY: 60,
          icon: 'task-icon-41',
          inPorts: [],
          outPorts: [{
            id: 'node4_5'
          }]
        },
        nodes: [{
          id: 'node1',
          name: '节点1',
          positionX: 115,
          positionY: 180,
          state: 'success',
          inPorts: [{
            id: 'node1_1',
            isConnected: true
          }],
          outPorts: [{
            id: 'node1_4'
          }]
        }, {
          id: 'node2',
          name: '节点2',
          positionX: 20,
          positionY: 300,
          state: 'mistake',
          inPorts: [{
            id: 'node2_1',
            isConnected: true
          }, {
            id: 'node2_3',
            isConnected: false
          }],
          outPorts: [{
            id: 'node2_4'
          }, {
            id: 'node2_5'
          }]
        }, {
          id: 'node3',
          name: '节点3',
          positionX: 340,
          positionY: 315,
          state: 'running',
          inPorts: [{
            id: 'node3_1',
            isConnected: true
          }, {
            id: 'node3_3',
            isConnected: false
          }],
          outPorts: [{
            id: 'node3_4'
          }]
        }, {
          id: 'node5',
          name: '节点5',
          positionX: 420,
          positionY: 420,
          icon: 'task-icon-6',
          inPorts: [{
            id: 'node5_1',
            isConnected: true
          }],
          outPorts: []
        }],
        paths: [{
          dotted: true,
          ptype: 'Q',
          startPort: 'node4_5',
          endPort: 'node1_1'
        }, {
          dotted: true,
          ptype: 'L',
          startPort: 'node1_4',
          endPort: 'node2_1'
        }, {
          dotted: false,
          ptype: 'Q',
          startPort: 'node1_4',
          endPort: 'node3_1'
        }, {
          dotted: false,
          ptype: 'L',
          startPort: 'node3_4',
          endPort: 'node5_1'
        }],
        propsData:[{
          attribute: 'paths',
          state: 'paths数据',
          mold: 'Array',
          defau: '-'
        }],
        methodData:[{
          methodname: 'vReload',
          exp: '刷新，重新渲染',
          retu: '-'
        }]
      }
    },
    methods: {
      selectlMethod: function (event, data, node) {
        console.log(event, data, node)
      },
      dragStart: function (event, node) {
        let nodeData = event.dataTransfer.getData('nodedata')
        console.log('开始移动', event.clientX, event.clientY, node, JSON.parse(nodeData))
        this.startNode = {id: node.id, positionX: event.clientX, positionY: event.clientY}
      },
      dragEnd: function (event, node) {
        let me = this
        console.log('移动结束', event.clientX, event.clientY, node)
        this.nodes.forEach(function (item) {
          if (item.id === node.id) {
            item.positionX = node.positionX + (event.clientX - me.startNode.positionX)
            item.positionY = node.positionY + (event.clientY - me.startNode.positionY)
          }
        })
        if (node.id === this.initialData.id) {
          this.initialData.positionX = node.positionX + (event.clientX - me.startNode.positionX)
          this.initialData.positionY = node.positionY + (event.clientY - me.startNode.positionY)
        }
      },
      dragOver: function (event) {
        console.log('移动中...', event.clientX, event.clientY)
      },
      updateCompleted: function () {
        console.log('updateCompleted!!')
        // 重新加载路径
        this.$refs.curve.vReload() //必需添加的方法，实现节点拖拽后，路径重新渲染
      },
      mouseMenu: function (event, id) {
        console.log(event, id)
      },
      mouseNodeMenu: function (event, node) {
        console.log(event, node)
      }
    }
  }
</script>
<style>
  .vnode-col{
    width: 1020px;
    height: 520px;
    /* text-align: center; */
    border: 1px solid;
    border-style: dashed;
    padding: 5px;
    margin-left: 5px;
    background-color: #eee;
  }
</style>



