<template>
  <Row>
    <Col span="21">
  <Row v-highlight>
    <Col>
    <div class="i-editor-wrapper"><div class="i-editor-md"> <div class="dev-md-content"><h3 id="YS_vNode">演示 <a href="#YS_vNode">#</a></h3></div></div></div>
    <div class="vnode-col">
      <task-work-area width=600 height=400 id="areaID" v-on:on-drag-over="dragOver" v-on:on-mouse="mouseMenu" ref="area">
        <task-common-node :node="node" v-on:on-select="selectlMethod" v-on:on-drag-start="dragStart" v-on:on-drag-end="dragEnd" :updateTem="updateCompleted" v-on:on-mouse="mouseNodeMenu"></task-common-node>
      </task-work-area>
    </div>
    </Col>
    <Col>
    <div class="i-editor-wrapper"><div class="i-editor-md"> <div class="dev-md-content"><h3 id="GS_vNode">概述 <a href="#GS_vNode">#</a></h3><p><code>&lt;task-common-node&gt;</code>是一个已经扩展的可以拥有多个输入 和 输出点的组件，其事件和<task-node>相同。</task-node></p>
      <h3 id="YF_vNode">用法 <a href="#YF_vNode">#</a></h3><pre><code class="lang-html">&lt;template&gt;
    &lt;div&gt;
      &lt;task-work-area width=600 height=400 id="areaID" v-on:on-drag-over="dragOver" v-on:on-mouse="mouseMenu" ref="area"&gt;
        &lt;task-common-node :node="node" v-on:on-select="selectlMethod" v-on:on-drag-start="dragStart" v-on:on-drag-end="dragEnd" :updateTem="updateCompleted" v-on:on-mouse="mouseNodeMenu"&gt;&lt;/task-common-node&gt;
      &lt;/task-work-area&gt;
    &lt;/div&gt;
&lt;/template&gt;
&lt;script&gt;
  export default {
    data () {
      return {
        startNode:null,
        node:{
          id: "node2",
          name: "节点2",
          positionX: 20,
          positionY: 100,
          state: "mistake",
          inPorts: [
            {
              id: "node2_1",
              isConnected: true
            },
            {
              id: "node2_3",
              isConnected: false
            }
          ],
          outPorts: [
            {
              id: "node2_4"
            },
            {
              id: "node2_5"
            }
          ]
        }
      }
    },
    methods: {
      selectlMethod: function (event, data, node) {
        console.log(event, data, node)
      },
      dragStart: function (event, node) {
        let nodeData = event.dataTransfer.getData('nodedata')
        console.log('开始移动', event.clientX, event.clientY, node, JSON.parse(nodeData))
        this.startNode = {id: node.id, positionX: event.clientX, positionY: event.clientY}
      },
      dragEnd: function (event, value) {
        let me = this
        if (value.id === this.node.id) {
          this.node.positionX = value.positionX + (event.clientX - me.startNode.positionX)
          this.node.positionY = value.positionY + (event.clientY - me.startNode.positionY)
        }
      },
      dragOver: function (event) {
        console.log('移动中...', event.clientX, event.clientY)
      },
      updateCompleted: function () {
        // 重新加载路径
        this.$refs.curve.vReload()
      },
      mouseMenu: function (event, id) {
        console.log(event, id)
      },
      mouseNodeMenu: function (event, node) {
        console.log(event, node)
      }
    }
  }
&lt;/script&gt;
</code></pre>
      <p>▲节点拖动解决思路：利用 <code>on-drag-start</code>和<code>v-on:on-drag-end</code>事件，开始拖动时记录节点的<code>(x1,y1)</code>坐标，拖拽结束记为<code>(x2,y2)</code>坐标，用坐标相<code>-</code> <code>(x2-x1) (y2-y1)</code>就得到节点在<code>XY</code>轴上的移动距离，此时改变<code>node</code>数据中的<code>positionX</code> 和<code>positionX</code>的值，组件重新渲染就得到了拖拽的效果了。</p>
      <p>▲之后的节点拖拽后的路径重新渲染则调用<code>:updateTem="updateCompleted"</code> 在node节点DOM加载完成后回调此方法，在此方法中调用路径的从新加载函数<code>this.$refs.[这里是路径组件的 ref的值].vReload()</code>，关于这个点请看路径组件使用教程。</p>

      <h3 id="API_vNode">API <a href="#API_vNode">#</a></h3></div></div>
      <p>props参数</p><br>
      <Table border size="small" :columns="propsColumn" :data="propsData"></Table>
      <br><p>event事件</p><br>
      <Table border size="small" :columns="eventColumn" :data="eventData"></Table>
    </div>
    <div class="i-editor-wrapper"><div class="i-editor-md"> <div class="dev-md-content"><h3 id="DATA_vNode">node数据结构 <a href="#DATA_vNode">#</a></h3>
      <pre><code class="lang-js">node: {
  id: [String, Number],//节点ID
  name: {
    type: [String, Number],
    default: '节点'
  },
  positionX: {//节点的X坐标
    type: [String, Number],
    default: 0
  },
  positionY: {//节点的Y坐标
    type: [String, Number],
    default: 0
  },
  icon: [String, Number],//图标
  state: {//状态可选值 [success,running,ready,mistake]
    type: [String, Number],
    default: 'ready'
  },
  inPorts: {//输入点的数据
    type: Array
  },
  outPorts: {//输出点的数据
    type: Array
  }
}
//inPorts,outPorts数据格式将在路径部分讲解
</code></pre>
    </div></div></div>
    </Col>
  </Row>
    </Col>
    <Col span="3">
    <Anchor show-ink>
      <AnchorLink v-for="item in lins" :key="item.uri" :href="'#'+ item.uri" :title="item.title" />
    </Anchor>
    </Col>
  </Row>
</template>
<script>
  import ApiTable from '../mixinx/ApiTable'
  export default {
    mixins: [ ApiTable],
    data () {
      return {
        lins:[{
          uri:'YS_vNode',
          title:'演示'
        },{
          uri:'GS_vNode',
          title:'概述'
        },{
          uri:'YF_vNode',
          title:'用法'
        },{
          uri:'API_vNode',
          title:'API'
        },{
          uri:'DATA_vNode',
          title:'node数据结构'
          }],
        startNode:null,
        node:{
          id: "node2",
          name: "节点2",
          positionX: 20,
          positionY: 100,
          state: "mistake",
          inPorts: [
            {
              id: "node2_1",
              isConnected: true
            },
            {
              id: "node2_3",
              isConnected: false
            }
          ],
          outPorts: [
            {
              id: "node2_4"
            },
            {
              id: "node2_5"
            }
          ]
        },
        propsData:[{
          attribute: 'width',
          state: '节点宽度',
          mold: 'String | Number',
          defau: '0'
        },{
          attribute: 'height',
          state: '节点高度',
          mold: 'String | Number',
          defau: '0'
        },{
          attribute: 'node',
          state: 'node类型的数据',
          mold: 'Array',
          defau: '-'
        },{
          attribute: 'updateTem',
          state: '节点组件更新完成后的回调函数，vue执行update时回调',
          mold: 'Function',
          defau: '-'
        }],
        eventData: [{
          eventname: 'on-drag-start',
          exp: '节点拖拽开始事件',
          retu: '( event, 所拖拽节点的node数据 )'
        },{
          eventname: 'on-drag-end',
          exp: '节点拖拽开始事件 ',
          retu: '( event, 所拖拽节点的node数据 )'
        },{
          eventname: 'on-select',
          exp: '节点选中事件',
          retu: '( event, 所拖拽节点的node数据, 节点的ref )'
        },{
          eventname: 'on-mouse',
          exp: '右击节点事件',
          retu: '( event, 所拖拽节点的node数据 )'
        }
        ]
      }
    },
    methods: {
      selectlMethod: function (event, data, node) {
        console.log(event, data, node)
      },
      dragStart: function (event, node) {
        let nodeData = event.dataTransfer.getData('nodedata')
        console.log('开始移动', event.clientX, event.clientY, node, JSON.parse(nodeData))
        this.startNode = {id: node.id, positionX: event.clientX, positionY: event.clientY}
      },
      dragEnd: function (event, value) {
        let me = this
        if (value.id === this.node.id) {
          this.node.positionX = value.positionX + (event.clientX - me.startNode.positionX)
          this.node.positionY = value.positionY + (event.clientY - me.startNode.positionY)
        }
      },
      dragOver: function (event) {
        console.log('移动中...', event.clientX, event.clientY)
      },
      updateCompleted: function () {
        // 重新加载路径
        this.$refs.curve.vReload()
      },
      mouseMenu: function (event, id) {
        console.log(event, id)
      },
      mouseNodeMenu: function (event, node) {
        console.log(event, node)
      }
    }
  }
</script>
<style>
  .vnode-col{
    width: 620px;
    height: 420px;
    /* text-align: center; */
    border: 1px solid;
    border-style: dashed;
    padding: 5px;
    margin-left: 5px;
    background-color: #eee;
  }
</style>



