<template>
  <Row>
    <Col span="21">
      <Row v-highlight>
        <div class="i-editor-wrapper"><div class="i-editor-md"> <div class="dev-md-content"><h3 id="YS_vNode">演示 <a href="#YS_vNode">#</a></h3></div></div></div>
        <div class="vnode-col">
          <task-work-area width=600 height=200 id="areaID" ref="area">
            <task-path :portData="portData"></task-path>
            <task-path :portData="portData1"></task-path>
          </task-work-area>
        </div>
        <div class="i-editor-wrapper"><div class="i-editor-md"> <div class="dev-md-content"><h3 id="GS_vNode">概述 <a href="#GS_vNode">#</a></h3><p><code>&lt;task-path&gt;</code>是一个点到点的路径，拥有<code>L</code>线<code>Q</code>线。<br><br><code>L</code>折线<code>M206 209 L 206 257, 82 257 T 82 305</code><br><br><code>Q</code>曲线<code>M206 209 Q 206 263, 304 263 T 402 320</code></p>
          <h3 id="YF_vNode">用法 <a href="#YF_vNode">#</a></h3><pre><code class="lang-html">&lt;template&gt;
        &lt;task-work-area width=600 height=400 id="areaID" ref="area"&gt;
            &lt;task-path :portData="portData"&gt;&lt;/task-path&gt;
            &lt;task-path :portData="portData1"&gt;&lt;/task-path&gt;
        &lt;/task-work-area&gt;
    &lt;/template&gt;
    &lt;script&gt;
      export default {
        data () {
          return {
            portData:{
              ptype:'Q',
              dotted:false,
              Mxy:{
                x:50,
                y:50
              },
              Txy:{
                x:100,
                y:100
              }
            },
            portData1:{
              ptype:'L',
              dotted:true,
              Mxy:{
                x:150,
                y:50
              },
              Txy:{
                x:200,
                y:100
              }
            }
          }
        }
      }
    &lt;/script&gt;
    </code></pre>
          <h3 id="API_vNode">API <a href="#API_vNode">#</a></h3></div></div>
          <p>props参数</p><br>
          <Table border size="small" :columns="propsColumn" :data="propsData"></Table>
        </div>
        <div class="i-editor-wrapper">
          <div class="i-editor-md">
            <div class="dev-md-content">
              <h3 id="DATA_vNode">portData数据结构 <a href="#DATA_vNode">#</a></h3>
              <pre>
                  <code class="lang-js">portData: {
      ptype: {//曲线或折线
        type: [String, Number],
        default: 'Q'
      },
      dotted: {//虚线或实线
        type: [String, Boolean],
        default: false
      },
      Mxy: {//开始点
        x: [String, Number],
        y: [String, Number]
      },
      Txy: {//结束点
        x: [String, Number],
        y: [String, Number]
      }
    }
    </code>
              </pre>
            </div>
          </div>
        </div>
      </Row>
    </Col>
    <Col span="3">
    <Anchor show-ink>
      <AnchorLink v-for="item in lins" :key="item.uri" :href="'#'+ item.uri" :title="item.title" />
    </Anchor>
    </Col>
  </Row>
</template>
<script>
  import ApiTable from '../mixinx/ApiTable'
  export default {
    mixins: [ ApiTable],
    data () {
      return {
        lins:[{
          uri:'YS_vNode',
          title:'演示'
        },{
          uri:'GS_vNode',
          title:'概述'
        },{
          uri:'YF_vNode',
          title:'用法'
        },{
          uri:'API_vNode',
          title:'API'
        },{
          uri:'DATA_vNode',
          title:'portData数据结构'
        }],
        portData:{
          ptype:'Q',
          dotted:false,
          Mxy:{
            x:50,
            y:50
          },
          Txy:{
            x:100,
            y:100
          }
        },
        portData1:{
          ptype:'L',
          dotted:true,
          Mxy:{
            x:150,
            y:50
          },
          Txy:{
            x:200,
            y:100
          }
        },
        propsData:[{
          attribute: 'portData',
          state: 'portData数据',
          mold: 'Object',
          defau: '-'
        }]
      }
    }
  }
</script>
<style>
  .vnode-col{
    width: 620px;
    height: 220px;
    /* text-align: center; */
    border: 1px solid;
    border-style: dashed;
    padding: 5px;
    margin-left: 5px;
    background-color: #eee;
  }
</style>



