<template>
  <Row>
    <Col span="21">
      <div>
        <div class="i-editor-wrapper"><div class="i-editor-md"> <div class="dev-md-content"><h3 id="GS_vNode">概述 <a href="#GS_vNode">#</a></h3><p><code>&lt;task-out-port&gt;</code>是<code>&lt;node&gt;</code>节点的一个输出点，主要在扩展节点时使用。</p>
          <h3 id="YF_vNode">用法 <a href="#YF_vNode">#</a></h3><p>具体使用请看 节点扩展</p>
          <h3 id="API_vNode">API <a href="#API_vNode">#</a></h3></div></div>
          <p>props参数</p><br>
          <Table border size="small" :columns="propsColumn" :data="propsData"></Table>
        </div>
      </div>
    </Col>
    <Col span="3">
    <Anchor show-ink>
      <AnchorLink v-for="item in lins" :key="item.uri" :href="'#'+ item.uri" :title="item.title" />
    </Anchor>
    </Col>
  </Row>
</template>
<script>
  import ApiTable from '../mixinx/ApiTable'
  export default {
    mixins: [ ApiTable],
    data () {
      return {
        lins:[{
          uri:'GS_vNode',
          title:'概述'
        },{
          uri:'YF_vNode',
          title:'用法'
        },{
          uri:'API_vNode',
          title:'API'
        }],
        propsData:[{
          attribute: 'pid',
          state: '输出点ID',
          mold: 'String | Number',
          defau: '-'
        },{
          attribute: 'content',
          state: '输出点描述',
          mold: 'String | Number',
          defau: '输出'
        }]
      }
    }
  }
</script>



