<template>
  <Row>
    <Col span="21">
      <div v-highlight>
    <div class="i-editor-wrapper">
      <div class="i-editor-md">
        <div class="dev-md-content">
          <h3 id="KZXJD_vNode">
            使用
            <code>&lt;task-node&gt;</code>
            扩展新节点
            <a href="#KZXJD_vNode">#</a>
          </h3>
          <pre>
            <code class="lang-html">&lt;template&gt;
    &lt;node :node="node" width=180 height=30 v-on:on-select="selectNodeMethod" v-on:on-drag-start="dragStart" v-on:on-drag-end="dragEnd" v-on:updateTem="updateTemp" v-on:on-mouse="mouseMenu"&gt;
    &lt;!-- 扩展节点、此处添加输入&lt;task-in-port&gt;和&lt;task-out-port&gt; 输入输出端点--&gt;
    &lt;!-- 节点中的事件和方法（selectNodeMethod,dragStart,dragEnd,updateTemp,mouseMenu）不需要去创建 --&gt;
    &lt;/node&gt;
    &lt;/template&gt;
    &lt;script&gt;
    import mixinsNode from 'vue-task-node/dist/node'

    export default {
      mixins: [ mixinsNode ],
      name: '扩展node名称',
      data () {
        return {
        }
      },
      props: {
        node: {
          id: [String, Number],
          name: {
            type: [String, Number],
            default: '节点'
          },
          positionX: {
            type: Number,
            default: 0
          },
          positionY: {
            type: Number,
            default: 0
          }
        }
      }
    }
    &lt;/script&gt;
    </code>
          </pre>
          <blockquote>
            <p>
              注意:节点中的事件和方法（
              <code>selectNodeMethod</code>
              ,
              <code>dragStart</code>
              ,
              <code>dragEnd</code>
              ,
              <code>updateTemp</code>
              ,
              <code>mouseMenu</code>
              ）不需要去创建，因为混入的
              <code>mixinsNode</code>
              扩展中已经实现了这几个方法。
            </p>
          </blockquote>
          <h3 id="API_vNode">API <a href="#API_vNode">#</a></h3>
          <p>扩展后的节点最少具有以下参数、属性、事件。。。其它根据自己情况而定</p>
        </div>
      </div>
      <p>props参数</p><br>
      <Table border size="small" :columns="propsColumn" :data="propsData"></Table>
      <br><p>event事件</p><br>
      <Table border size="small" :columns="eventColumn" :data="eventData"></Table>
    </div>
  </div>
    </Col>
    <Col span="3">
    <Anchor show-ink>
      <AnchorLink v-for="item in lins" :key="item.uri" :href="'#'+ item.uri" :title="item.title" />
    </Anchor>
    </Col>
  </Row>
</template>
<script>
  import ApiTable from '../mixinx/ApiTable'
  export default {
    mixins: [ ApiTable],
    data () {
      return {
        lins:[{
          uri:'KZXJD_vNode',
          title:'扩展新节点'
        },{
          uri:'API_vNode',
          title:'API'
        }],
        propsData:[{
          attribute: 'width',
          state: '节点宽度',
          mold: 'String | Number',
          defau: '0'
        },{
          attribute: 'height',
          state: '节点高度',
          mold: 'String | Number',
          defau: '0'
        },{
          attribute: 'node',
          state: 'node类型的数据',
          mold: 'Array',
          defau: '-'
        },{
          attribute: 'updateTem',
          state: '节点组件更新完成后的回调函数，vue执行update时回调',
          mold: 'Function',
          defau: '-'
        }],
        eventData: [{
          eventname: 'on-drag-start',
          exp: '节点拖拽开始事件',
          retu: '( event, 所拖拽节点的node数据 )'
        },{
          eventname: 'on-drag-end',
          exp: '节点拖拽开始事件 ',
          retu: '( event, 所拖拽节点的node数据 )'
        },{
          eventname: 'on-select',
          exp: '节点选中事件',
          retu: '( event, 所拖拽节点的node数据, 节点的ref )'
        },{
          eventname: 'on-mouse',
          exp: '右击节点事件',
          retu: '( event, 所拖拽节点的node数据 )'
        }
        ]
      }
    }
  }
</script>



