<template>
  <Row>
    <Col span="21">
      <div v-highlight>
        <div class="i-editor-wrapper">
          <div class="i-editor-md">
            <div class="dev-md-content">
              <h3 id="GS_vNode">概述 <a href="#GS_vNode">#</a></h3>
              <p>
                <code>&lt;task-node&gt;</code>
                是一个用来扩展多样式可拖拽的节点，其绑定了节点的拖拽事件、左击事件、右击事件等。
              </p>
              <h3 id="YF_vNode">用法 <a href="#YF_vNode">#</a></h3>
              <p>具体使用请看 节点扩展</p>
              <h3 id="API_vNode">API <a href="#API_vNode">#</a></h3>
            </div>
          </div>
          <p>props参数</p><br>
          <Table border size="small" :columns="propsColumn" :data="propsData"></Table>
          <br><p>event事件</p><br>
          <Table border size="small" :columns="eventColumn" :data="eventData"></Table>
        </div>
        <div class="i-editor-wrapper">
          <div class="i-editor-md">
            <div class="dev-md-content">
              <h3 id="DATA_vNode">node数据结构 <a href="#DATA_vNode">#</a></h3>
              <pre>
            <code class="lang-js">node: {
      id: [String, Number], //节点ID
      positionX: {//节点的X坐标
        type: Number,
        default: 0
      },
      positionY: {//节点的Y坐标
        type: Number,
        default: 0
      }
    }
    </code>
          </pre>
            </div>
          </div>
        </div>
      </div>
    </Col>
    </Col>
    <Col span="3">
    <Anchor show-ink>
      <AnchorLink v-for="item in lins" :key="item.uri" :href="'#'+ item.uri" :title="item.title" />
    </Anchor>
    </Col>
  </Row>
</template>
<script>
  import ApiTable from '../mixinx/ApiTable'
  export default {
    mixins: [ ApiTable],
    data () {
      return {
        lins:[{
          uri:'YS_vNode',
          title:'演示'
        },{
          uri:'GS_vNode',
          title:'概述'
        },{
          uri:'YF_vNode',
          title:'用法'
        },{
          uri:'API_vNode',
          title:'API'
        },{
          uri:'DATA_vNode',
          title:'node数据结构'
        }],
        propsData:[{
          attribute: 'width',
          state: '节点宽度',
          mold: 'String | Number',
          defau: '0'
        },{
          attribute: 'height',
          state: '节点高度',
          mold: 'String | Number',
          defau: '0'
        },{
          attribute: 'node',
          state: 'node类型的数据',
          mold: 'Array',
          defau: '-'
        }],
        eventData: [{
          eventname: 'on-drag-start',
          exp: '节点拖拽开始事件',
          retu: '( event, 所拖拽节点的node数据 )'
        },{
          eventname: 'on-drag-end',
          exp: '节点拖拽开始事件 ',
          retu: '( event, 所拖拽节点的node数据 )'
        },{
          eventname: 'on-select',
          exp: '节点选中事件',
          retu: '( event, 所拖拽节点的node数据, 节点的ref )'
        },{
          eventname: 'on-mouse',
          exp: '右击节点事件',
          retu: '( event, 所拖拽节点的node数据 )'
        }
        ]
      }
    }
  }
</script>



