    </Col>
    <Col span="3">
      <Anchor show-ink>
        <AnchorLink v-for="item in lins" :key="item.uri" :href="'#'+ item.uri" :title="item.title" />
      </Anchor>
    </Col>
  </Row>
</template>
<script>
  export default {
    data () {
      return {
        lins:[{
          uri:'GY_vNode',
          title:'关于'
        }]
      }
    }
  }
</script>



