<template>
  <Row>
    <Col span="21">
      <div class="i-editor-wrapper"><div class="i-editor-md"> <div class="dev-md-content">
        <h3 id="CSB_vNode">CodeSandBox在线演示 <a href="#CSB_vNode">#</a></h3>
      </div></div>
        <iframe src="https://codesandbox.io/embed/2wv588orlr?fontsize=14" style="width:100%; height:500px; border:0; border-radius: 4px; overflow:hidden;" sandbox="allow-modals allow-forms allow-popups allow-scripts allow-same-origin"></iframe>
      </div>
    </Col>
    <Col span="3">
    <Anchor show-ink>
      <AnchorLink v-for="item in lins" :key="item.uri" :href="'#'+ item.uri" :title="item.title" />
    </Anchor>
    </Col>
  </Row>
</template>
<script>
  export default {
    data() {
      return {
        lins: [{
          uri: 'CSB_vNode',
          title: 'CodeSandBox在线演示'
        }]
      }
    }
  }
  </script>



